import React, { useState } from 'react';

let srcset = [200, 400, 600, 1200, 1800];

const ProductCard = ({ product }) => {
  let image = product.images[0];
  let variant = product.variants.find((item) => item.available);
  // console.log(image);

  return (
    <div className="Card Card--product">
      <figure className="Card__media">
        <a href={`/collections/available-now/products/${product.handle}`}>
          <ProductImage data={image}></ProductImage>
        </a>
      </figure>
      <div className="Card__details">
        <h4 className="Card__title">
          <a href={`/collections/available-now/products/${product.handle}`}>{product.title}</a>
        </h4>
        <div data-price-wrapper>
          <span className="price">${variant.price}</span>
        </div>
      </div>
    </div>
  );
};

const ProductImage = ({ data }) => {
  let image = data.src;
  let firstSplit = image.split('?v=');
  let baseName = firstSplit[0].slice(0, -4);
  let extension = firstSplit[0].slice(-4);

  let sizes = [200, 400, 800, 1600];

  return (
    <img
      data-src={`${baseName}_200x${extension}`}
      data-srcset={sizes.map((size) => `${baseName}_${size}x${extension} ${size}w,`)}
      className="lazyload"
    />
  );
};

export default ProductCard;
